html {
  box-sizing: border-box;
  margin:0;
  padding:0;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  margin:0;
  box-sizing: border-box;
  font-family: 'Mulish', sans-serif;
  letter-spacing: 0.05rem;
  color: #181717;
  
}

.light-theme {
  background-color: #fff;
  margin:0;
  padding:0;
}

.dark-theme {
  background-color: #2a292c;
  color: #f8f8f8;
  margin:0;
  padding:0;
}

.dark-theme nav {
  background-color: #2a292c;
}

.light-theme a {
  text-decoration: none; 
  color: #181717;
}

.light-theme a:hover  {
  color: #FC5050;
}

.dark-theme a  {
  text-decoration: none; 
  color: #eeedee;
}

.dark-theme a:hover  {
  color: #FC5050;
}

.header, .light-theme .header {
  display:flex;
  flex-wrap:nowrap;
  justify-content: space-evenly;
  align-items: center;
  height: 28rem;
  background-color: #eeeeee ;
  padding: 0 0vw;
}

.dark-theme .header {
  height: 28rem;
  background-color:#34353a;
  padding: 1rem;
}

h1, .light-theme h1 {
  margin:0;
  font-family: 'Poppins', sans-serif;
  color: #5c5c62;
}

.dark-theme h1 {
  margin:0;
  color:#f8f8f8;
}

button {
  border:none;
  background: transparent;
  cursor:pointer;
}

.headerIntro {
  display: flex;
  justify-content: center;
  align-items: center;
  letter-spacing: 0.1em;
  line-height: 2em;
}

.headerText, .light-theme .headerText {
  color: #34353a;
  font-size: 1rem;
  font-weight: 200;
  line-height:1.6em;
  max-width: 45vw;
}

.dark-theme .headerText {
  color:#eeedee;
}

.hello {
  display:flex;
  align-items: center;
  column-gap: 1rem;
}

footer {
  position:sticky;
  display:flex;
  justify-content: center;
  margin-top:1rem;
}

footer a {
  padding: 1rem;
}

/*NAVIGATION*/

.desktopThemeToggle {
  display:none;
  box-sizing: border-box;
}

nav {
  display: grid;
  row-gap: 1rem;
  padding: 1.5rem 10vw;
  font-family: 'Poppins', sans-serif;
}

nav>:nth-child(1) {
  justify-self: right;
}

nav>:nth-child(2) {
  justify-self: center;
}

.menuItems {
  display: inline-flex;
  justify-content: space-evenly;
  column-gap: 5vw;
}

/*HOME*/

.tableWrapper {
  display: flex;
  justify-content: center;
}

table {
  border: none;
  text-align: left;
  width: 100%;
}

td {
  max-width: fit-content;
  border-collapse: collapse;
  padding: 1rem;
}

  td a {
    font-weight:bold;
  }

 .tags {
   display:flex;
   gap:1rem;
   flex-wrap: wrap;
   align-items: center;
   font-size:14px;
  }

 .tags>span {
   background-color:#eeeeee;
   padding:5px;
   border-radius: 5px;
   font-weight: 700;
 }

 .tags>span:hover {
  background-color:#dbdada;
  cursor:pointer;
  color:#000;
 }

.dark-theme .tags>span {
  background-color:#34353a;
}

.dark-theme .tags>span:hover {
  background-color:#4a4b52;
  cursor:pointer;
  color:#fff;
}

  /*PAGES*/

h3 {
  margin: 0;
}

.contentContainer {
padding: 0 10vw;
background-color: #fff;
min-height: 80vh;
}

.dark-theme .contentContainer {
  background-color: #2a292c;
  color: #f8f8f8;
  }

.hr {
  margin:auto;
  width:100%;
  margin-bottom:1rem;
  height:2px;
  background-color: #eeedee;
  padding: 0 15vw;
}

.dark-theme .hr {
  background-color: #34353a;
  height:2px;
}

.content {
  box-sizing: border-box;
  text-align:justify;
  line-height: 2em;
}

/*DIARY*/
  .diaryPost, .light-theme .diaryPost {
    background-color: #eeeeee;
    border-radius: 10px;
    padding: 3rem;
  }

  .dark-theme .diaryPost {
    background-color: #34353a;
  }

  .diaryDate {
    display:flex;
    justify-content: flex-start;
  }

/*ARTICLES - MAIN AND TOPICS*/
  .articlesTopic {
    display:flex;
    column-gap: 2rem;
    align-items: center;
  }

  .date {
    margin: 1rem 0;
    font-size: smaller;
  }

  h3 {
    padding-top:1rem;
  }

/*ARTICLES - PAGES*/
  p {
    margin-bottom: 2rem;
  }

  .headerArticle {
    box-sizing: border-box;
    margin-bottom:2rem;
  }

  .headerArticleInfo{
    font-size:smaller;
  }

  .currentCrumb {
    color: #FC5050;
  }

  .headerArticle h2 {
    text-align: initial;
  }
  .content a {
    font-weight:bold;
  }

  .contentArticle {
    text-align: justify;
    line-height: 2em;
  }

  .contentArticle a {
    color:#FC5050;
  }


/*PORTFOLIO*/

  .contentPortfolio img {
    box-sizing: border-box;
    opacity: 0.5;
    width:80vw;
  }

  .contentPortfolio img:hover {
    opacity: 1;
  }

/*ABOUT*/
  #me {
    padding: 1rem 2rem;
  }

/*SMALL AND DESKTOP SCREENS*/
  @media (max-width: 335px) {
    .menuItems {
      box-sizing: border-box;
      align-items: center;
      flex-flow:column;
      row-gap:0.5rem;
    } 
  
    nav {
      font-size: 16px;
      box-sizing: border-box;
    }
    body {
      font-size: 14px;
    }
    .tags{
      gap:0.5rem;
    }
    .tags>span {
      font-size:8px;
    }

    .headerText, .light-theme .headerText {
      font-weight: 200;
    }
   }

   @media (max-width: 350px) {
    #me {
      width: 65%;
      float:left;
      padding: 1rem;
    }
   }

   @media (max-width: 360px){
     
    .content {
      text-align:initial;
    }
   }

   
  @media (min-width: 460px) {
    #me {
      float:left;
    }

    .headerText, .light-theme .headerText {
      font-weight: 200;
    }
   }

   @media (max-width: 500px) {
    td a {
      font-size:smaller;
    }

    td time, .tags {
      font-size:small;
    }
    #contentPortfolio img {
      width: 100%;
    }

    .headerAnimation{
      display:none !important;
    }

  }

   @media (min-width: 700px) {
    h2 {
    text-align: left;
    }

    .headerText, .light-theme .headerText{
      font-size:25px;
    }

    .contentContainer {
      font-size: 20px;
    }

    .tags {
      flex-wrap: nowrap;
     }

     .headerArticle {
      margin-bottom:3rem;
    }

   }

   @media (min-width: 60rem) {
    .desktopThemeToggle {
      display:inline-block;
      box-sizing: border-box;
    }
  
    nav {
      box-sizing: border-box;
      grid-template-columns: 1.5fr 1fr auto;
      row-gap: 0;
    }
  
    nav>:nth-child(1) {
      grid-column-start: 3;
      display:none;
    }
  
    nav>:nth-child(2) {
      justify-self: left;
    
    }
  
    nav>:nth-child(3) {
      align-self: center;
      font-size: 25px;
    }
  
    nav>:nth-child(4) {
      grid-column-start: 3;
  
    }
  
    .menuItems {
      box-sizing: border-box;
      column-gap: 4vw;
      margin: 0 5vw;
    }
  
    body {
      box-sizing: border-box;
    }
  
    .headerMain>span:nth-child(1){
      max-width: 50rem;
      padding: 5rem;
    }

    .contentContainer {
      padding: 0 17vw;
    }

    .contentPortfolio img {
      box-sizing: border-box;
      width:470px;
      height:300px;
    }
  }

    @media (max-width: 905px) {
    .headerText, .light-theme .headerText {
      font-size: 1rem;
      line-height:1.3em;
      max-width: 50vw;
    }
  }

  /*
  Dots
 */
 .headerAnimation {
   display:flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   gap: 1rem;
 }

.dots {
  position: relative;
  width: 15px;
  height: 15px;
  border-radius: 10px;
  background-color: #9880ff;
  color: #9880ff;
  animation: dots 1s infinite linear alternate;
  animation-delay: .6s;
}

.dots::before, .dots::after {
  content: '';
  display: inline-block;
  position: absolute;
  top: 0;
}

.dots::before {
  left: -20px;
  width: 15px;
  height: 15px;
  border-radius: 10px;
  background-color: #9880ff;
  color: #9880ff;
  animation: dots 1s infinite alternate;
  animation-delay: 0s;
}

.dots::after {
  left: 20px;
  width: 15px;
  height: 15px;
  border-radius: 10px;
  background-color: #9880ff;
  color: #9880ff;
  animation: dots 1s infinite alternate;
  animation-delay: 1s;
}

@keyframes dots {
  0% {
    background-color: #9880ff;
  }
  50%,
  100% {
    background-color: #ebe6ff;
  }
}

@media (max-width: 500px) {
  .dots {
    width: 8px;
    height: 8px;
  }
  .dots::before {
    left: -12px;
    width: 8px;
    height: 8px;
  }

  .dots::after {
    left: 12px;
    width: 8px;
    height: 8px;
   
  }

}
